import { defaultContainer } from '../core/defaultContainer';
import { PropsConfig } from '../models/config/props-config';
/**
 * @param {?=} config
 * @return {?}
 */
export function model(config?: PropsConfig[]) {
    return function (
        target: Object
    ) {
        defaultContainer.addPropsConfig(target,config)
    } 
}

